import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmYoutube,
  VmEmbed 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-youtube', VmYoutube);
define('vm-embed', VmEmbed);
  

export interface YoutubeProps extends JSX.VmYoutube, HTMLAttributes<HTMLVmYoutubeElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmYoutubeElement, YoutubeProps>(
  'vm-youtube',
  new Set(['cookies','videoId','showFullscreenControl','poster','language','autoplay','controls','logger','loop','muted','playsinline']),
);
  